import Util from '../../util/common'
import * as types from '../types'

// 
const state = {
	fetchLoading: false, //Loading
	isColect:false,//
	carnum:1//
}

// store : mutation
const mutations = {
	// loading
	[types.SET_LOADING](state, res) {
		state.fetchLoading = res
	},
	//
	[types.SET_COLECT](state, res) {
		state.isColect = res
	},
	//
	[types.SET_CARNUM](state, res) {
		state.carnum = res
	}
}
export default {
	state,
	mutations
}


