<template>
	<div id="mySet">
		<ul class="set-nav bbline">
			<li class="item">
				<div class="avatar tap" @click="onAvatar">
					<span class="name"></span>
					<img :src="avatar">
					<i class="iconfont icon-a12r"></i>
				</div>
			</li>

			<li class="item">
				<router-link to="/mySet/myInfo" class="tap">
					<span class="name"></span>
					<span class="dsp"></span>
					<i class="iconfont icon-a12r"></i>
				</router-link>
			</li>
			<li class="item">
				<router-link to="/mySet/myVerified" class="tap">
					<span class="name"></span>
					<span class="dsp green"></span>
					<i class="iconfont icon-a12r"></i>
				</router-link>
			</li>
			<li class="item">
				<router-link to="/mySet/myPhone" class="tap">
					<span class="name"></span>
					<span class="dsp">182****9808</span>
					<i class="iconfont icon-a12r"></i>
				</router-link>
			</li>
			<li class="item">
				<router-link to="/mySet/myPassword" class="tap">
					<span class="name"></span>
					<span class="dsp"></span>
					<i class="iconfont icon-a12r"></i>
				</router-link>
			</li>
		</ul>

		<ul class="set-nav btline bbline mt10">
			<li class="item">
				<div class="tap">
					<span class="name"></span>
					<span class="dsp">10.22MB</span>
					<i class="iconfont icon-a12r"></i>
				</div>
			</li>
			<li class="item">
				<div class="tap">
					<span class="name"></span>
					<mt-switch v-model="isDefault"></mt-switch>
				</div>
			</li>
		</ul>

		<ul class="set-nav btline bbline mt10">
			<li class="item">
				<router-link to="/mySet/about" class="tap">
					<span class="name"></span>
					<i class="iconfont icon-a12r"></i>
				</router-link>
			</li>
			<li class="item">
				<router-link to="/mySet/help" class="tap">
					<span class="name"></span>
					<i class="iconfont icon-a12r"></i>
				</router-link>
			</li>
			<li class="item">
				<div class="tap">
					<span class="name"></span>
					<span class="dsp">V1.0</span>
					<i class="iconfont icon-a12r"></i>
				</div>
			</li>
		</ul>

		<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>

	</div>
</template>

<script>
	import { Actionsheet } from 'mint-ui';
	export default {
		data() {
			return {
				isDefault: false,
				avatar: "../../../static/images/avatar.gif",
				sheetVisible: false,
				actions: [{
						name: ""
					},
					{
						name: ""
					}
				]
			}
		},
		methods: {
			onAvatar: function() {
				this.sheetVisible = !this.sheetVisible;
			}
		}
	}
</script>
<style scoped="scoped">
	@import '../../assets/css/set-nav.css';
	.mint-switch {
		position: absolute;
		right: .24rem;
		top: 50%;
		margin-top: -16px;
	}
</style>


